header{
	.meny{
		font-size: 178%;
	}
	.stang{
		font-size: 178%;
	}
	.site-branding{
		
	    margin-left: 41px;
	}
}
.front-header{
	background-color: transparent;
	padding: 10px;
	img {
     	max-width: 100%;
    	height: auto;
    	background-size: 100% auto ;
	}
	
	nav{
		text-align: right;
		
		width: 100%;
		margin-top: 14px;


		
		ul{
			    margin-top: 15px;
			    position:absolute;
		}

		li{
			
			
		}
		a{
			display: block;
			color: white;
			
		}
		a:hover{
			text-decoration: none;
		}

		.btn.focus, .btn:focus, .btn:hover{
			    color: #23527c;
		}
	}

}

.headerColor{
	background-color: #343434;
}

.headerColorScroll{
	background-color: #343434;
}

#storNav{
	height: 160px;
	hr{
		
	    border-top: solid #0cf;
	}
}




.screen-img {
	margin-top:50px;
	p {
		margin: 0px 0px 0px 0px;
	}
	
	img {

    	max-width: 100%;
    	height: auto;
    	min-width: 100%
	}
	    

}

#block-screenimgtext{
	position: absolute;
    left: 31%;
    top: 47%;    
    width: 40%;
    font-size: 20px;
    color: white;
    text-align: center;
    
}

@media screen and (max-width: 605px) {
    #block-screenimgtext{
	position: absolute;
    left: 31%;
    top: 47%;    
    width: 40%;
    font-size: 13px;
    color: white;
    text-align: center;
    
	}
}
@media screen and (max-width: 460px) {
    #block-screenimgtext{
	position: absolute;
    left: 31%;
    top: 36%;
    width: 40%;
    font-size: 10px;
    color: white;
    text-align: center;
    
	}
}

@media screen and (max-width: 377px) {
    #block-screenimgtext{
	    position: absolute;
    left: 31%;
    top: 24%;
    width: 40%;
    font-size: 10px;
    color: white;
    text-align: center;
    
	}
}


