	display: table;
  	margin: 0 auto;

  	h1{
  		text-align: center;
  		margin:50px;
  	}

  	.searchField{
  		height: 32.5px;
    	margin: 0px 12px 5px 12px;
    	color: #00c7fc;
  	}

	img{
		width: 250px;
	}
	.divs{
		width: 250px;
		text-align: center;


	}
	label {
    margin: 0px 12px 5px 12px;
    border-radius: 0px;
    color: #fff;
    background-color: #00c7fc;
    border-color: #05b4e2;
	}
	.ng-scope{
		display: inline-block;
		margin: 15px;

	}
	.centerDiv{
		display: table;
    	margin: 0 auto;
	}
}



