.block-page-title-block{
	text-align: center;
	hr{
		width: 84px;
	    margin-top: -10px;
	    border-top: solid #0cf;
	}
}

.node__content{
	padding:0% 5% 0% 5%;
	.field--name-field-ingress{
		display: table;
    	margin: auto;
	    margin-bottom: 54px;
    	margin-top: -23px;
	}
	.field--type-image{
		width: 99%;
	}
	img{
		display: block;
    	margin: 0 auto;
    	width:50%;
    	margin-bottom: 25px;
	}
	.field__label{
		display: none;
	}
}

.kontakt-for-test{
	//border:1px solid red;
	form{
		//border: 1px solid red;
	    width: 91%;
	    display: table;
	    margin: auto;
	}

	.inner{
			//border:1px solid green;
		  	
		div{
			//border:1px solid blue;
			display: inline-block;
			min-width: 220px;
    		width: 43%;
    		margin-left: 4%;
		}
	}

	#edit-actions{
		display: table;
	    margin: auto;
	    width: 0px;
	}

	
	
	input{		
		border:none;
		border-bottom: 1px solid #ccc;
		margin-right: 10%;
	}	

	#edit-submit{
    	background-image: linear-gradient(to bottom, #00c3ff, #00c3ff);
    	color: white;
    	display: table;
    	margin: auto;
    	width: 41%;
	    height: 37px;
	    border-radius: 19px;
	}

	.text{
		
	    margin-bottom: 1%;
	    min-width: 400px;
	}
}